// Client
import {
  HomePage,
  AboutPage,
  ProjectDetailsPage,
  NoMatchPage,
  TestPage
} from "../js/client";

// Guideline
import {
  GlColorPalettes,
  GlTypography,
  GlButtons,
  GlForms,
  GlToast,
  GlShadows
} from "../js/guideline";

import { path_ROOT, path_ABOUT, path_PROJECT } from "./paths.config";

const routes = [
  // Client
  {
    // path: "/",
    path: path_ROOT,
    exact: true,
    component: HomePage
  },
  {
    path: path_ABOUT,
    component: AboutPage
  },
  {
    path: path_PROJECT,
    component: ProjectDetailsPage
  },
  {
    path: "/test",
    component: TestPage
  },

  // Guideline
  {
    path: "/guideline",
    exact: true,
    component: GlColorPalettes
  },
  {
    path: "/guideline/typography",
    component: GlTypography
  },
  {
    path: "/guideline/buttons",
    component: GlButtons
  },
  {
    path: "/guideline/forms",
    component: GlForms
  },
  {
    path: "/guideline/toast",
    component: GlToast
  },
  {
    path: "/guideline/shadows",
    component: GlShadows
  },

  // NoMatch
  {
    component: NoMatchPage
  }
];

export default routes;
